.page-index {
    text-align: center;
    padding-top: 2em;

    .photo {
        width: 100%;
        height: 100%;
        max-width: 200px;
        max-height: 200px;
        border-radius: 50%;
        // border: solid 1px lighten($primary-font-color,50%)
        // -webkit-box-shadow: 0px 0px 20px 0px rgba(255,255,255,0.4)
        // -moz-box-shadow: 0px 0px 20px 0px rgba(255,255,255,0.4)
 }        // box-shadow: 0px 0px 20px 0px rgba(255,255,255,0.4)

    p.hi {
        font-size: 1.7em;
        line-height: 2em;
        font-family: $secondary-font;
        margin-bottom: .5em;
        font-size: 300;
        .name {
            color: $primary-color;
            font-weight: 400; } }

    p.aboutMeInfo {
        padding-top: 1em; }

    p.aboutMe {
        a {
            color: $primary-color; } } }

.page-blog {
    .year, .categories, .tags, #categoryName {
        color: $secondary-color;
        font-weight: 400;
        font-family: $secondary-font;
        font-size: .9em;
        text-transform: uppercase; }

    ul {
        list-style: none;
        padding-inline-start: 0;
        margin-top: 0; }

    .allCategories {
        li {
            a {
                cursor: pointer;
                border-bottom: 0px solid #000; } } }

    .article {
        margin: 0 0 2em 0;
        .title {
            line-height: 1.6em;
            a {
                border-bottom: none;
                &:hover {
                    .icon-link {
                        color: $primary-color; } } } }
        .info {
            font-size: .7em;
            .category {
