header {
    font-size: .9rem;
    font-weight: 400;
    font-family: $secondary-font;
    padding-top: 1.5em;
    padding-bottom: 1em;

    .logo {
        font-weight: 400;
        text-decoration: none;
        border-bottom: none;
        font-size: 1rem;
        color: $primary-color;

        transition: color 200ms, border-bottom 200ms;
        -webkit-transition: color 200ms, border-bottom 200ms; }

    .logo:hover {
        border-bottom: 1px solid $primary-color;
        transition: color 200ms, border-bottom 200ms;
        -webkit-transition: color 200ms, border-bottom 200ms; }

    .line {
        color: $primary-font-color;
        margin: 0 .5em; }

    .txt-rotate {
        color: $primary-font-color;
        font-size: .8em; }

    .social {
        text-align: right;
        font-size: 1.4rem;

        .separacion {
            margin-left: 1em; }

        a {
            border-bottom: none;
            color: $primary-font-color; }

        a:hover {
            color: $primary-color; } } }

/* Selectors and styles affecting screens UP TO 480px (Phone) */
@media (max-width: $breakpoint-small) {
    .line, .txt-rotate, .blinking-cursor {
        display: none;
        visibility: hidden; } }
