// This is the single file output by sass. It is intended to ONLY @import other files.

// 1 - Tools
// Importar: Fuentes, reset (normalize), variables
@import '1-tools/fonts';
@import '1-tools/normalize';
@import '1-tools/simple-grid';
@import '1-tools/vars';

// 2 - Basics
//Importar: Estilos para etiquetas html comunes; por ejemplo a, img, blockquote ...
@import '2-basics/body';
@import '2-basics/p';
@import '2-basics/a';
@import '2-basics/strong';
@import '2-basics/blockquote';
@import '2-basics/hr';

// 3 - Modules
// Importar: Estilos creados por el diseñador; por ejemplo clases, id.
@import '3-modules/blinking-cursor';
@import '3-modules/jump';
@import '3-modules/fadein';

// 4 - Sections
//Importar: Estilos para secciones comunes de páginas; por ejemplo nav, header, footer ...
@import '4-sections/header';
@import '4-sections/footer';

// 5 - Pages
//Importar: Estilos para páginas; por ejemplo index, about, projects ...
@import '5-pages/index';
@import '5-pages/archive';
@import '5-pages/post';
@import '5-pages/404';
