.jump{
    display: inline-block;
    -webkit-animation: jump .75s infinite alternate;
    -moz-animation: jump .75s infinite alternate;
    animation: jump .75s infinite alternate;
}
@-webkit-keyframes jump {
    0% { transform: translateY(3px); }
    100% { transform: translateY(-3px); }
}
@-moz-keyframes jump{
    0% { transform: translateY(3px); }
    100% { transform: translateY(-3px); }
}
@keyframes jump {
    0% { transform: translateY(3px); }
    100% { transform: translateY(-3px); }
}