.page-post {
    // margin-top: 2em

    .postInfo {
        text-align: center;
        margin: 2.5em 0;
        .title {
            font-size: 2em;
            font-weight: 400;
            line-height: 1.4em;
            color: $primary-color;
            font-family: $secondary-font; }

        .info {
            font-size: .7em;
            .icon-minus {
                margin: 0 .5em;
                color: lighten($primary-font-color,50%); } } }

    .postContent {
        h1, h2, h3, h4, h5, h6 {
            font-weight: 700;
            margin-block-start: 4rem;
            margin-block-end: 2.5rem; }

        p {
            display: block;
            margin-block-start: 1em;
            margin-block-end: 2.5em;
            margin-inline-start: 0px;
            margin-inline-end: 0px; }

        img {
            max-width: 100%;
            -webkit-border-radius: .75em;
            border-radius: .75em; }

        a {
            color: $primary-color; }

        figure {
            margin: 0;
            text-align: center;

            figcaption {
                font-size: .85em;
                line-height: 1.8em;
                padding: 0 1.5em 2em 1.5em; } }

        .key {
            font-weight:      400;
            background-color: #f7f7f7;
            border:           1px solid #ccc;
            color:            #333;
            font-size:        .8em;
            line-height:      1.4em;
            text-shadow:      0 1px 0 #fff;
            display:          inline-block;
            padding:          0.1em 0.6em;
            margin:           0 0.1em;
            white-space:      nowrap;

            -webkit-box-shadow: 0 1px 0px rgba(0,0,0,0.2),0 0 0 2px #fff inset;
            -moz-box-shadow:    0 1px 0px rgba(0,0,0,0.2),0 0 0 2px #fff inset;
            box-shadow:         0 1px 0px rgba(0,0,0,0.2),0 0 0 2px #fff inset;

            -webkit-border-radius: 3px;
            -moz-border-radius:    3px;
            border-radius:         3px; }

        .terminal {
            background: #262626;
            color: white;
            padding: 1em;
 }            // font-family: 'Maven Pro', sans-serif

        .information,.warning {
            border-left: 4px solid #f9f9f9;
            background: #000;
            padding: 1em; }

        .information {
            border-left-color: $third-color; }

        .warning {
            border-left-color: $third-color; }

        .shareAndDonation {
            // text-align: center
            margin-bottom: 1em;
            padding: 0 1em;
            font-size: .9rem;
            .message {
                text-align: center;
                display: block; } }

        .bmc-button img {
            height: 34px !important;
            width: 35px !important;
            margin-bottom: 1px !important;
            box-shadow: none !important;
            border: none !important;
            vertical-align: middle !important; }

        .bmc-button {
            padding: 3px 10px 3px 10px !important;
            line-height: 35px !important;
            height: 51px !important;
            text-decoration: none !important;
            display:inline-flex !important {}
            color:#ffffff !important {}
            background-color:#F2505D !important {}
            border-radius: 5px !important;
            border: 1px solid transparent !important;
            padding: 7px 15px 7px 10px !important;
            font-size: .8em !important;
            font-weight: 400;
            letter-spacing:-0.08px !important {}
            box-shadow: 0px 1px 2px rgba(190, 190, 190, 0.5) !important;
            -webkit-box-shadow: 0px 1px 2px 2px rgba(190, 190, 190, 0.5) !important;
            margin: 0 auto !important;
            // font-family:'Lato', sans-serif !important
            -webkit-box-sizing: border-box !important;
            box-sizing: border-box !important; }

        .bmc-button:hover, .bmc-button:active, .bmc-button:focus {
            -webkit-box-shadow: 0px 1px 2px 2px rgba(190, 190, 190, 0.5) !important;
            text-decoration: none !important;
            box-shadow: 0px 1px 2px 2px rgba(190, 190, 190, 0.5) !important;
            opacity: 0.85 !important;
            color:#ffffff !important {} } } }
