* {
    //text-rendering: optimizeLegibility !important
    text-rendering: geometricPrecision;
    -webkit-font-smoothing: antialiased !important;
    -webkit-text-size-adjust: 100% !important;
    -moz-osx-font-smoothing: grayscale;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; }

html,body {
    cursor: default;
    // background: #0d0221 //#f5f7fb
    background: rgb(86,46,98);
    background: -moz-linear-gradient(319deg, rgba(86,46,98,1) 0%, rgba(22,30,63,1) 100%);
    background: -webkit-linear-gradient(319deg, rgba(86,46,98,1) 0%, rgba(22,30,63,1) 100%);
    background: linear-gradient(319deg, rgba(86,46,98,1) 0%, rgba(22,30,63,1) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#562e62",endColorstr="#161e3f",GradientType=1);
    color: $primary-font-color !important;
    font-family: $primary-font;
    font-weight: 300;
    font-size: 16px !important;
    line-height: 2rem;
    text-align: left; }

#Up {
    height: 1px; }
